<template>
<div>
    <v-container>
        <v-row class="d-flex justify-center mt-6">
            <v-col cols="6">
                <!-- Formulario Login -->
                <template v-if="isLogin">
                    <div class="d-flex justify-start py-5">
                        <h1>Login de Usuario</h1>
                    </div>
                    <v-form ref="form" v-model="valid" lazy-validation d-flex justify-center @submit.prevent="doLogin">
                        <!-- Inputs -->
                        <v-text-field v-model="userData.email" :rules="rules.emailRules" label="E-mail" required outlined></v-text-field>

                        <v-text-field v-model="userData.password" :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" :rules="rules.password" :type="show ? 'text' : 'password'" label="Password" outlined @click:append="show = !show"></v-text-field>
                        <!-- End Inputs -->

                        <!-- Botones -->
                        <div class="d-flex justify-space-between">
                            <v-btn type="submit" :disabled="!valid" color="success" class="mr-7">
                                Enter
                            </v-btn>
                            <v-btn color="error" class="mr-4" @click="resetData">
                                Reiniciar Form
                            </v-btn>

                            <div class="ml-1 text-center">
                                <a href="#" @click="isLogin = false">¿No tienes una cuenta?</a>
                            </div>
                        </div>
                        <!-- End Botones -->
                    </v-form>
                </template>
                <!-- End Formulario Login -->

                <!-- Formulario Register -->
                <template v-else>
                    <div class="d-flex justify-start py-5">
                        <h1>Registrar</h1>
                    </div>
                    <v-form ref="form" v-model="valid" lazy-validation d-flex justify-center @submit.prevent="doRegister">
                        <!-- Inputs -->
                        <v-text-field v-model="userData.name" :counter="10" :rules="rules.nameRules" label="Name" required outlined py="3"></v-text-field>

                        <v-text-field v-model="userData.email" :rules="rules.emailRules" label="E-mail" required outlined></v-text-field>

                        <v-text-field v-model="userData.password" :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'" :rules="rules.password" :type="show ? 'text' : 'password'" name="input-10-1" label="Password" outlined @click:append="show = !show"></v-text-field>
                        <!-- End Inputs -->

                        <!-- Botones -->
                        <div class="d-flex justify-space-between">
                            <v-btn type="submit" :disabled="!valid" color="success" class="mr-7">
                                Confirmar
                            </v-btn>
                            <v-btn color="error" class="mr-2" @click="resetData">
                                Reset Form
                            </v-btn>
                            <div class="text-center">
                                <a href="#" @click="isLogin = true">¿Quieres iniciar sesión?</a>
                            </div>

                        </div>
                        <!-- End Botones -->

                    </v-form>
                </template>
                <!-- End Formulario Register -->
            </v-col>
        </v-row>
    </v-container>
</div>
</template>

<script>
export default {
    data: () => ({
        isLogin: true,
        valid: true,
        userData: {
            name: "",
            email: "",
            password: "",
        },
        rules: {
            nameRules: [
                (v) => !!v || "Campo Oblgatorio",
                (v) =>
                (v && v.length <= 10) || "Nombre debe tener menos de 10 caracteres",
            ],

            emailRules: [
                (v) => !!v || "Campo Oblgatorio",
                (v) => /.+@.+\..+/.test(v) || "E-mail debe ser válido",
            ],

            password: [
                (v) => !!v || "Campo Oblgatorio.",
                (v) => (v && v.length >= 6) || "Min 6 caracteres",
                // emailMatch: () => `The email and password you entered don't match`,
            ],
        },

        show: false,
    }),

    methods: {
        redirect() {
            this.$router.push({
                name: "Cursos"
            });
        },
        resetData() {
            this.userData.name = this.userData.email = this.userData.password = "";
        },
        async doRegister() {
            console.log("registro");

            try {
                await this.$store.dispatch("user/doRegister", {
                    name: this.userData.name,
                    email: this.userData.email,
                    password: this.userData.password,
                });
                console.log("Cuenta creada");
                this.resetData();
                this.redirect();
            } catch (error) {
                console.error(error.message);
            }
        },

        async doLogin() {
            console.log("login");
            try {
                await this.$store.dispatch("user/doLogin", {
                    email: this.userData.email,
                    password: this.userData.password,
                });
                console.log("Logged in");
                this.resetData();
                this.redirect();
            } catch (error) {
                console.error(error.message);
            }
        },
    },
};
</script>
